<template>
  <div>
    <el-tabs class="full-width"
             v-model="activeName"
    >
      <el-tab-pane
        label="Thuc tnh phin bn"
        name="variant"
      >
        <div class="row">
          <div class="col-lg-4 col-md-6 col-sm-6 col-xs-12 card-attribute" v-for="variantAttribute of variantAttributes">
            <div class="card">
              <div class="card-header">
                <h6 class="card-title">{{variantAttribute.name}}</h6>
              </div>
              <hr>
              <div class="card-body card-body-attribute">
                <el-table
                  :data="variantAttribute.child"
                  style="width: 100%">
                  <el-table-column
                    prop="name"
                    label="Tiu "
                    width="280">
                  </el-table-column>
                  <el-table-column
                    :min-width="120"
                    fixed="right"
                    class-name="td-actions"
                    label="Hnh ng"
                  >
                    <template slot-scope="props">
                      <p-button
                        type="primary" size="sm" icon @click="showModal(props.row, 'variant', 'edit')"
                      >
                        <i class="nc-icon nc-ruler-pencil"></i>
                      </p-button>
                      <p-button
                        type="danger" size="sm" icon @click="remove(props.$index, props.row, 'variant')"
                      >
                        <i class="nc-icon nc-simple-remove"></i>
                      </p-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <hr>
              <div class="card-body">
                <el-button type="primary" size="small" class="full-width" @click="showModal(variantAttribute, 'variant', 'add')">Thm mi</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane
        label="Thuc tnh sn phm"
        name="product"
      >
        <div class="row">
          <div class="col-lg-4 col-md-6 col-sm-6 col-xs-12 card-attribute" v-for="productAttribute of productAttributes">
            <div class="card">
              <div class="card-header">
                <h6 class="card-title">{{productAttribute.name}}</h6>
              </div>
              <hr>
              <div class="card-body card-body-attribute">
                <el-table
                  :data="productAttribute.child"
                  style="width: 100%">
                  <el-table-column
                    prop="name"
                    label="Tiu "
                    width="280">
                  </el-table-column>
                  <el-table-column
                    :min-width="120"
                    fixed="right"
                    class-name="td-actions"
                    label="Hnh ng"
                  >
                    <template slot-scope="props">
                      <p-button
                        type="primary" size="sm" icon @click="showModal(props.row, 'product', 'edit')"
                      >
                        <i class="nc-icon nc-ruler-pencil"></i>
                      </p-button>
                      <p-button
                        type="danger" size="sm" icon @click="remove(props.$index, props.row, 'product')"
                      >
                        <i class="nc-icon nc-simple-remove"></i>
                      </p-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
              <hr>
              <div class="card-body">
                <el-button type="primary" size="small" class="full-width" @click="showModal(productAttribute, 'product', 'add')">Thm mi</el-button>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane
        label="Mu sc"
        name="color"
        v-if="colorAttributes.length"
      >
        <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12 card-attribute">
          <div class="card">
            <div class="card-header">
              <h6 class="card-title">Mu sc</h6>
            </div>
            <hr>
            <div class="card-body card-body-attribute">
              <el-table
                :data="colorAttributes"
                style="width: 100%">
                <el-table-column
                  prop="name"
                  label="Tiu "
                  minWidth="180">
                </el-table-column>
                <el-table-column
                  prop="value"
                  label="Gi tr"
                  minWidth="120"
                >
                  <template slot-scope="props">
                    <input type="color" :value="props.row['value']" :name="props.row['name']" @change="changeColor">
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <modal :show.sync="dialogAddVisible" headerClasses="justify-content-center">
      <h4 slot="header" class="title title-up m-0">Thuc tnh</h4>
      <div class="row">
        <el-form :model="formCreateAttribute" class="full-width">
          <el-form-item label="Tiu ">
            <el-input
              v-model="formCreateAttribute.name"
              :class="errors.has('name')?'border-danger':''"
              v-validate="'required'"
              data-vv-name="name"
              data-vv-as="Tiu "
            ></el-input>
            <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
          </el-form-item>
        </el-form>
      </div>
      <div class="text-right">
        <p-button @click.native="dialogAddVisible = false">ng</p-button>
        <p-button type="primary" @click="addAttribute">{{ this.action == 'add' ? 'Thm thuc tnh' : 'Cp nht'}}</p-button>
      </div>
    </modal>
  </div>
</template>

<script>
  import {Table, TableColumn, Select, Option, Button, Tabs, TabPane, Input, MessageBox, Message, Form, FormItem} from 'element-ui'
  import { mapState } from 'vuex'
  import { Modal } from 'src/components/UIComponents'

  export default {
    components: {
      ElTable: Table,
      ElTableColumn: TableColumn,
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElTabs: Tabs,
      ElTabPane: TabPane,
      ElInput: Input,
      ElForm: Form,
      ElFormItem: FormItem,
      Message,
      MessageBox,
      Modal
    },
    data() {
      return {
        activeName: 'variant',
        selectAttribute: [],
        selectType: '',
        action: '',
        formCreateAttribute: {
          name: ''
        },
        dialogAddVisible: false
      }
    },
    computed: mapState(['variantAttributes', 'productAttributes', 'colorAttributes']),
    mounted() {
      this.$store.dispatch('fetchProductAttributes');
      this.$store.dispatch('setPageTitle', 'Danh sch thuc tnh');
    },
    methods: {
      showModal(attribute, type, action){
        this.selectAttribute = attribute;
        this.selectType = type;
        this.action = action;
        if (action == 'edit'){
          this.formCreateAttribute.name = attribute.name;
        } else this.formCreateAttribute.name = '';
        this.dialogAddVisible = true;
      },
      addAttribute(){
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            let data = {};
            data.name = self.formCreateAttribute.name;
            data.type = self.selectType;
            if (self.action == 'add') {
              data.parent_id = self.selectAttribute.id;
              self.$store.dispatch('storeAttribute', data).then(res => {
                Message({
                  message: 'To mi thnh cng',
                  type: 'success'
                });
              }, res => {
                Message({
                  message: res.message,
                  type: 'error'
                });
              });
            }
            else {
              data.id = self.selectAttribute.id;
              data.parent_id = self.selectAttribute.parent_id;
              self.$store.dispatch('updateAttribute', data).then(res => {
                Message({
                  message: 'Cp nht thnh cng',
                  type: 'success'
                });
              }, res => {
                Message({
                  message: res.message,
                  type: 'error'
                });
              });
            }
            self.dialogAddVisible = false;
            self.selectAttribute = [];
            self.formCreateAttribute = [];
            self.selectType = '';
            self.action = '';
          }
        })
      },
      remove(index, row, type){
        MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
          confirmButtonText: 'ng ',
          cancelButtonText: 'Hy b',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removeAttribute', {
            id: row.id,
            type: type
          }).then(res => {
            Message({
              message: ' xa thuc tnh',
              type: 'success'
            });
          }, res => {
            Message({
              message: "C li xy ra",
              type: 'error'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hy b'
          });
        })
      },
      changeColor(event){
        let target = event.target;
        let name = target.name;
        let handle = this.$util.createHandle(target.name);
        let value = target.value;
        let data = {
          name: name,
          handle: handle,
          value: value
        }
        this.$store.dispatch('updateColor', data).then(resp => {
          Message({
            message: 'Chnh sa thnh cng',
            type: 'success'
          });
        });
      }
    },
  }
</script>

<style scoped>
  .card-body-attribute{
    height: 300px;
    overflow-y: auto;
  }
</style>
